import React, { useState } from "react";

export default function TextForm(props) {
  const handleUpClick = () => {
    let newText = text.toUpperCase();
    setText(newText);
  };

  const handleLoClick = () => {
    let newText = text.toLowerCase();
    setText(newText);
  };

  const handleClearClick = () => {
    let newText = "";
    setText(newText);
  };

  const handleCopyClick = () => {
    var newText = document.getElementById("myBox");
    text.select();
    navigator.clipboard.writeText(newText.value);
  };

  const handleRemoveSpaceClick = () => {
    var newText = text.split(/[ ]+/);
    setText(newText.join(" "));
  };

  const handleOnChange = (event) => {
    setText(event.target.value);
  };

  const [text, setText] = useState("");
  return (
    <>
      <div className="mb-3 container my-3">
        <h2>{props.heading}</h2>
        <textarea className="form-control" value={text} onChange={handleOnChange} placeholder="Enter Text here" id="myBox" rows="8"></textarea>
        <button className="btn btn-primary my-3" onClick={handleUpClick}>
          Convert to Uppercase
        </button>
        <button className="btn btn-primary my-3 mx-3" onClick={handleLoClick}>
          Convert to Lowercase
        </button>
        <button className="btn btn-primary my-3" onClick={handleClearClick}>
          Clear Text
        </button>
        <button className="btn btn-primary my-3 mx-3" onClick={handleCopyClick}>
          Copy Text
        </button>
        <button className="btn btn-primary my-3" onClick={handleRemoveSpaceClick}>
          Remove Extra Spaces
        </button>
      </div>
      <div className="container my-3">
        <h2>Your Text Summery</h2>
        <p>
          {text.split(" ").length} Words and {text.length} Characters
        </p>
        <p>{0.008 * text.split(" ").length} Minutes Read</p>
        <h3>Preview</h3>
        <p>{text}</p>
      </div>
    </>
  );
}
